import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { toast } from 'react-hot-toast';
import useDNIVerification from '../hooks/useDNIVerification';

const FirstSetup = ({ onSetupComplete }) => {
    const [formData, setFormData] = useState({
        dni: '',
        nombre: '',
        apellido: '',
        password: '',
        confirmPassword: ''
    });
    const [loading, setLoading] = useState(false);
    const { verifyDNI, loading: dniLoading, userData: dniData, error: dniError } = useDNIVerification();

    // Verificar DNI cuando cambie y tenga suficientes dgitos
    useEffect(() => {
        if (formData.dni && formData.dni.length >= 6) {
            const timeoutId = setTimeout(async () => {
                const data = await verifyDNI(formData.dni);
                if (data) {
                    setFormData(prev => ({
                        ...prev,
                        nombre: data.nombre,
                        apellido: data.apellido
                    }));
                    toast.success('Datos encontrados en sistema central');
                }
            }, 500); // Debounce de 500ms

            return () => clearTimeout(timeoutId);
        }
    }, [formData.dni, verifyDNI]);

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        if (formData.password !== formData.confirmPassword) {
            toast.error('Las contraseas no coinciden');
            return;
        }

        if (formData.password.length < 6) {
            toast.error('La contrasea debe tener al menos 6 caracteres');
            return;
        }

        setLoading(true);

        try {
            const response = await axios.post('/api/admin/setup/first-admin', {
                dni: formData.dni,
                nombre: formData.nombre,
                apellido: formData.apellido,
                password: formData.password
            });

            toast.success('Primer administrador creado exitosamente!');
            
            // Llamar al callback para indicar que el setup est completo
            if (onSetupComplete) {
                onSetupComplete(response.data);
            }
        } catch (error) {
            console.error('Error creando primer admin:', error);
            toast.error(error.response?.data?.message || 'Error al crear administrador');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
            <div className="sm:mx-auto sm:w-full sm:max-w-md">
                <div className="flex justify-center">
                    <div className="bg-green-100 p-3 rounded-full">
                        <svg className="w-12 h-12 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6V4m0 2a2 2 0 100 4m0-4a2 2 0 110 4m-6 8a2 2 0 100-4m0 4a2 2 0 100 4m0-4v2m0-6V4m6 6v10m6-2a2 2 0 100-4m0 4a2 2 0 100 4m0-4v2m0-6V4" />
                        </svg>
                    </div>
                </div>
                <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
                    Configuracin Inicial
                </h2>
                <p className="mt-2 text-center text-sm text-gray-600">
                    Crea el primer usuario administrador del sistema
                </p>
            </div>

            <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
                <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
                    <div className="mb-6 p-4 bg-blue-50 border border-blue-200 rounded-md">
                        <div className="flex">
                            <div className="flex-shrink-0">
                                <svg className="h-5 w-5 text-blue-400" fill="currentColor" viewBox="0 0 20 20">
                                    <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                                </svg>
                            </div>
                            <div className="ml-3">
                                <h3 className="text-sm font-medium text-blue-800">
                                    Primera configuracin
                                </h3>
                                <div className="mt-2 text-sm text-blue-700">
                                    <p>
                                        Este usuario tendr privilegios de administrador y podr gestionar otros usuarios del sistema.
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>

                    <form className="space-y-6" onSubmit={handleSubmit}>
                        <div>
                            <label htmlFor="dni" className="block text-sm font-medium text-gray-700">
                                DNI / Cdigo de Usuario
                            </label>
                            <div className="mt-1 relative">
                                <input
                                    id="dni"
                                    name="dni"
                                    type="text"
                                    required
                                    value={formData.dni}
                                    onChange={handleInputChange}
                                    className="appearance-none block w-full px-3 py-2 pr-10 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                                    placeholder="Ingrese su DNI o cdigo"
                                />
                                {/* Indicador de verificacin */}
                                <div className="absolute inset-y-0 right-0 pr-3 flex items-center">
                                    {dniLoading && formData.dni.length >= 6 && (
                                        <svg className="animate-spin h-5 w-5 text-blue-500" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                                            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                        </svg>
                                    )}
                                    {dniData && !dniLoading && (
                                        <svg className="h-5 w-5 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                                            <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                                        </svg>
                                    )}
                                    {!dniData && !dniLoading && formData.dni.length >= 6 && (
                                        <svg className="h-5 w-5 text-yellow-500" fill="currentColor" viewBox="0 0 20 20">
                                            <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                                        </svg>
                                    )}
                                </div>
                            </div>
                            {/* Mensajes de estado */}
                            {dniData && (
                                <p className="mt-1 text-sm text-green-600">
                                     Usuario encontrado en sistema central
                                </p>
                            )}
                            {!dniData && !dniLoading && formData.dni.length >= 6 && (
                                <p className="mt-1 text-sm text-yellow-600">
                                     Usuario no encontrado en sistema central - se crear nuevo registro
                                </p>
                            )}
                            {dniError && (
                                <p className="mt-1 text-sm text-red-600">
                                     {dniError}
                                </p>
                            )}
                        </div>

                        <div>
                            <label htmlFor="nombre" className="block text-sm font-medium text-gray-700">
                                Nombre
                            </label>
                            <div className="mt-1">
                                <input
                                    id="nombre"
                                    name="nombre"
                                    type="text"
                                    required
                                    value={formData.nombre}
                                    onChange={handleInputChange}
                                    className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                                    placeholder="Ingrese su nombre"
                                />
                            </div>
                        </div>

                        <div>
                            <label htmlFor="apellido" className="block text-sm font-medium text-gray-700">
                                Apellido
                            </label>
                            <div className="mt-1">
                                <input
                                    id="apellido"
                                    name="apellido"
                                    type="text"
                                    required
                                    value={formData.apellido}
                                    onChange={handleInputChange}
                                    className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                                    placeholder="Ingrese su apellido"
                                />
                            </div>
                        </div>

                        <div>
                            <label htmlFor="password" className="block text-sm font-medium text-gray-700">
                                Contrasea
                            </label>
                            <div className="mt-1">
                                <input
                                    id="password"
                                    name="password"
                                    type="password"
                                    required
                                    value={formData.password}
                                    onChange={handleInputChange}
                                    className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                                    placeholder="Mnimo 6 caracteres"
                                />
                            </div>
                        </div>

                        <div>
                            <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700">
                                Confirmar Contrasea
                            </label>
                            <div className="mt-1">
                                <input
                                    id="confirmPassword"
                                    name="confirmPassword"
                                    type="password"
                                    required
                                    value={formData.confirmPassword}
                                    onChange={handleInputChange}
                                    className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                                    placeholder="Repita la contrasea"
                                />
                            </div>
                        </div>

                        <div>
                            <button
                                type="submit"
                                disabled={loading}
                                className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
                            >
                                {loading ? (
                                    <div className="flex items-center">
                                        <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                                            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                        </svg>
                                        Creando Administrador...
                                    </div>
                                ) : (
                                    'Crear Administrador'
                                )}
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
};

export default FirstSetup;
