import React, { useState, useEffect } from 'react';
import { 
    History, 
    Search, 
    Filter, 
    CheckCircle, 
    XCircle, 
    Calendar,
    Clock,
    MessageSquare,
    User,
    Phone,
    ChevronLeft,
    ChevronRight,
    RefreshCw
} from 'lucide-react';

const MessageHistory = () => {
    const [historial, setHistorial] = useState([]);
    const [loading, setLoading] = useState(true);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);
    const [searchTerm, setSearchTerm] = useState('');
    const [filterSuccess, setFilterSuccess] = useState('all'); // 'all', 'success', 'failed'
    
    const itemsPerPage = 20;

    useEffect(() => {
        loadHistorial();
    }, [currentPage]);

    useEffect(() => {
        // Reiniciar a pgina 1 cuando cambie el filtro o bsqueda
        if (currentPage !== 1) {
            setCurrentPage(1);
        } else {
            loadHistorial();
        }
    }, [searchTerm, filterSuccess]);

    const loadHistorial = async () => {
        setLoading(true);
        try {
            // Obtener fecha de hoy en la zona horaria local, no UTC
            const today = new Date();
            // Crear fecha local sin convertir a UTC
            const year = today.getFullYear();
            const month = String(today.getMonth() + 1).padStart(2, '0');
            const day = String(today.getDate()).padStart(2, '0');
            const todayStr = `${year}-${month}-${day}`;
            
            console.log(` Fecha local calculada: ${todayStr}`);
            console.log(` Fecha actual completa: ${today.toString()}`);
            console.log(` Fecha ISO (UTC): ${today.toISOString()}`);
            
            const params = new URLSearchParams({
                page: currentPage.toString(),
                limit: itemsPerPage.toString(),
                fecha: todayStr // Filtrar solo por hoy
            });

            console.log(` URL historial: ${process.env.REACT_APP_API_URL}/api/whatsapp/historial?${params}`);

            const response = await fetch(`${process.env.REACT_APP_API_URL}/api/whatsapp/historial?${params}`, {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                }
            });

            if (response.ok) {
                const data = await response.json();
                console.log(` Datos del historial recibidos:`, data);
                if (data.success) {
                    setHistorial(data.historial);
                    setTotalPages(data.totalPages);
                }
            }
        } catch (error) {
            console.error('Error cargando historial:', error);
        } finally {
            setLoading(false);
        }
    };

    const formatDate = (dateString) => {
        // Crear fecha desde el string UTC y convertir a hora local
        const date = new Date(dateString);
        return date.toLocaleDateString('es-ES', {
            year: 'numeric',
            month: 'short',
            day: 'numeric',
            timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone
        });
    };

    const formatTime = (dateString) => {
        // Crear fecha desde el string UTC y convertir a hora local
        const date = new Date(dateString);
        return date.toLocaleTimeString('es-ES', {
            hour: '2-digit',
            minute: '2-digit',
            timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone
        });
    };

    const filteredHistorial = historial.filter(item => {
        const matchesSearch = !searchTerm || 
            item.numeros_enviados.toLowerCase().includes(searchTerm.toLowerCase()) ||
            item.mensaje_enviado.toLowerCase().includes(searchTerm.toLowerCase());
        
        const matchesFilter = filterSuccess === 'all' || 
            (filterSuccess === 'success' && item.estado === 'enviado') ||
            (filterSuccess === 'failed' && item.estado === 'error');
        
        return matchesSearch && matchesFilter;
    });

    const getStatusIcon = (estado) => {
        return estado === 'enviado' ? (
            <CheckCircle className="h-4 w-4 text-green-500" />
        ) : (
            <XCircle className="h-4 w-4 text-red-500" />
        );
    };

    const getStatusText = (exitoso) => {
        return exitoso ? 'Enviado' : 'Fallido';
    };

    const getStatusColor = (exitoso) => {
        return exitoso ? 'text-green-600' : 'text-red-600';
    };

    const handlePageChange = (newPage) => {
        if (newPage >= 1 && newPage <= totalPages) {
            setCurrentPage(newPage);
        }
    };

    const getStats = () => {
        const total = historial.length;
        const exitosos = historial.filter(item => item.estado === 'enviado').length;
        const fallidos = historial.filter(item => item.estado === 'error').length;
        
        return { total, exitosos, fallidos };
    };

    const stats = getStats();

    return (
        <div className="space-y-6">
            {/* Header */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-4">
                        <div className="p-3 bg-purple-100 rounded-full">
                            <History className="h-6 w-6 text-purple-600" />
                        </div>
                        <div>
                            <h2 className="text-xl font-semibold text-gray-900">Historial de Mensajes</h2>
                            <p className="text-sm text-gray-600">
                                Registro de envos de hoy - {new Date().toLocaleDateString('es-ES', {
                                    weekday: 'long',
                                    year: 'numeric',
                                    month: 'long',
                                    day: 'numeric'
                                })}
                            </p>
                        </div>
                    </div>
                    
                    <button
                        onClick={loadHistorial}
                        disabled={loading}
                        className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                    >
                        <RefreshCw className={`h-4 w-4 mr-2 ${loading ? 'animate-spin' : ''}`} />
                        Actualizar
                    </button>
                </div>
            </div>

            {/* Estadsticas rpidas */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                    <div className="flex items-center space-x-3">
                        <div className="p-2 bg-blue-100 rounded-lg">
                            <MessageSquare className="h-5 w-5 text-blue-600" />
                        </div>
                        <div>
                            <p className="text-sm font-medium text-gray-600">Total</p>
                            <p className="text-2xl font-bold text-gray-900">{stats.total}</p>
                        </div>
                    </div>
                </div>

                <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                    <div className="flex items-center space-x-3">
                        <div className="p-2 bg-green-100 rounded-lg">
                            <CheckCircle className="h-5 w-5 text-green-600" />
                        </div>
                        <div>
                            <p className="text-sm font-medium text-gray-600">Exitosos</p>
                            <p className="text-2xl font-bold text-green-600">{stats.exitosos}</p>
                        </div>
                    </div>
                </div>

                <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                    <div className="flex items-center space-x-3">
                        <div className="p-2 bg-red-100 rounded-lg">
                            <XCircle className="h-5 w-5 text-red-600" />
                        </div>
                        <div>
                            <p className="text-sm font-medium text-gray-600">Fallidos</p>
                            <p className="text-2xl font-bold text-red-600">{stats.fallidos}</p>
                        </div>
                    </div>
                </div>
            </div>

            {/* Filtros y bsqueda */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                <div className="flex flex-col sm:flex-row space-y-4 sm:space-y-0 sm:space-x-4">
                    {/* Bsqueda */}
                    <div className="flex-1">
                        <div className="relative">
                            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                                <Search className="h-4 w-4 text-gray-400" />
                            </div>
                            <input
                                type="text"
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                                className="block w-full pl-10 pr-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                                placeholder="Buscar por contacto, nmero o mensaje..."
                            />
                        </div>
                    </div>

                    {/* Filtro de estado */}
                    <div className="sm:w-48">
                        <div className="relative">
                            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                                <Filter className="h-4 w-4 text-gray-400" />
                            </div>
                            <select
                                value={filterSuccess}
                                onChange={(e) => setFilterSuccess(e.target.value)}
                                className="block w-full pl-10 pr-8 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent appearance-none bg-white"
                            >
                                <option value="all">Todos</option>
                                <option value="success">Exitosos</option>
                                <option value="failed">Fallidos</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>

            {/* Lista de historial */}
            <div className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden">
                {loading ? (
                    <div className="p-8 text-center">
                        <RefreshCw className="h-8 w-8 text-gray-400 mx-auto mb-4 animate-spin" />
                        <p className="text-gray-500">Cargando historial...</p>
                    </div>
                ) : filteredHistorial.length === 0 ? (
                    <div className="p-8 text-center">
                        <MessageSquare className="h-8 w-8 text-gray-400 mx-auto mb-4" />
                        <p className="text-gray-500">No se encontraron mensajes</p>
                        {searchTerm && (
                            <p className="text-xs text-gray-400 mt-2">
                                Intenta con otros trminos de bsqueda
                            </p>
                        )}
                    </div>
                ) : (
                    <div className="divide-y divide-gray-200">
                        {filteredHistorial.map((item, index) => (
                            <div key={index} className="p-6 hover:bg-gray-50 transition-colors">
                                <div className="flex items-start justify-between">
                                    <div className="flex-1 min-w-0">
                                        <div className="flex items-center space-x-3 mb-2">
                                            {getStatusIcon(item.estado)}
                                            <div className="flex items-center space-x-2">
                                                <Phone className="h-4 w-4 text-gray-400" />
                                                <span className="font-medium text-gray-900">
                                                    {item.numeros_enviados}
                                                </span>
                                            </div>
                                            <div className="flex items-center space-x-2">
                                                <span className="text-sm text-gray-600">
                                                    {item.cantidad_numeros} nmero(s)
                                                </span>
                                            </div>
                                        </div>
                                        
                                        <div className="mb-3">
                                            <p className="text-sm text-gray-800 line-clamp-2">
                                                {item.mensaje_enviado}
                                            </p>
                                        </div>
                                        
                                        <div className="flex items-center space-x-4 text-xs text-gray-500">
                                            <div className="flex items-center space-x-1">
                                                <Calendar className="h-3 w-3" />
                                                <span>{formatDate(item.fecha_envio)}</span>
                                            </div>
                                            <div className="flex items-center space-x-1">
                                                <Clock className="h-3 w-3" />
                                                <span>{formatTime(item.fecha_envio)}</span>
                                            </div>
                                        </div>
                                    </div>
                                    
                                    <div className="ml-4 flex-shrink-0 text-right">
                                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                                            item.estado === 'enviado' 
                                                ? 'bg-green-100 text-green-800' 
                                                : 'bg-red-100 text-red-800'
                                        }`}>
                                            {getStatusText(item.estado === 'enviado')}
                                        </span>
                                        {item.estado === 'error' && (
                                            <p className="text-xs text-red-600 mt-1 max-w-32 truncate" title="Error en envo">
                                                Error en envo
                                            </p>
                                        )}
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </div>

            {/* Paginacin */}
            {totalPages > 1 && (
                <div className="bg-white rounded-xl shadow-sm p-4 border border-gray-200">
                    <div className="flex items-center justify-between">
                        <div className="text-sm text-gray-700">
                            Pgina {currentPage} de {totalPages}
                        </div>
                        
                        <div className="flex items-center space-x-2">
                            <button
                                onClick={() => handlePageChange(currentPage - 1)}
                                disabled={currentPage === 1}
                                className="inline-flex items-center px-3 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                            >
                                <ChevronLeft className="h-4 w-4 mr-1" />
                                Anterior
                            </button>
                            
                            <span className="text-sm text-gray-500">
                                {currentPage} / {totalPages}
                            </span>
                            
                            <button
                                onClick={() => handlePageChange(currentPage + 1)}
                                disabled={currentPage === totalPages}
                                className="inline-flex items-center px-3 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                            >
                                Siguiente
                                <ChevronRight className="h-4 w-4 ml-1" />
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default MessageHistory;
