import React from 'react';
import { 
    BarChart3,
    Calendar, 
    TrendingUp, 
    Users, 
    MessageCircle,
    CheckCircle,
    XCircle,
    Clock
} from 'lucide-react';

const UserStats = ({ userStats, onRefresh }) => {
    // Debug - verificar qu datos estamos recibiendo
    console.log(' UserStats recibi:', userStats);
    
    const progressPercentage = userStats.limiteDiario > 0 
        ? (userStats.enviadosHoy / userStats.limiteDiario) * 100 
        : 0;
    
    console.log(' Clculo del progreso:', {
        enviadosHoy: userStats.enviadosHoy,
        limiteDiario: userStats.limiteDiario,
        progressPercentage: progressPercentage,
        progressPercentageCalculated: (userStats.enviadosHoy / userStats.limiteDiario) * 100
    });
    
    console.log(` Calculando progreso: ${userStats.enviadosHoy}/${userStats.limiteDiario} = ${progressPercentage}%`);

    const getProgressColor = () => {
        if (progressPercentage < 50) return 'bg-green-500';
        if (progressPercentage < 80) return 'bg-yellow-500';
        return 'bg-red-500';
    };

    const getProgressTextColor = () => {
        if (progressPercentage < 50) return 'text-green-600';
        if (progressPercentage < 80) return 'text-yellow-600';
        return 'text-red-600';
    };

    return (
        <div className="space-y-6">
            {/* Header */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                <div className="flex items-center space-x-4">
                    <div className="p-3 bg-blue-100 rounded-full">
                        <BarChart3 className="h-6 w-6 text-blue-600" />
                    </div>
                    <div>
                        <h2 className="text-xl font-semibold text-gray-900">Estadsticas de Usuario</h2>
                        <p className="text-sm text-gray-600">Resumen de tu actividad de mensajera</p>
                    </div>
                </div>
            </div>

            {/* Estadsticas principales */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                {/* Restantes */}
                <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-sm font-medium text-gray-600">Restantes</p>
                            <p className={`text-2xl font-bold mt-1 ${getProgressTextColor()}`}>
                                {userStats.restantes}
                            </p>
                        </div>
                        <div className={`p-3 rounded-full ${
                            userStats.restantes > 50 ? 'bg-green-100' : 
                            userStats.restantes > 20 ? 'bg-yellow-100' : 'bg-red-100'
                        }`}>
                            <CheckCircle className={`h-6 w-6 ${
                                userStats.restantes > 50 ? 'text-green-600' : 
                                userStats.restantes > 20 ? 'text-yellow-600' : 'text-red-600'
                            }`} />
                        </div>
                    </div>
                    <div className="mt-4">
                        <div className="flex items-center space-x-2">
                            <TrendingUp className="h-4 w-4 text-gray-400" />
                            <span className="text-xs text-gray-500">
                                Disponibles hoy
                            </span>
                        </div>
                    </div>
                </div>
            </div>

            

            {/* Informacin adicional */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Estado actual */}
                <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                    <h3 className="text-lg font-semibold text-gray-900 mb-4">
                        Estado Actual
                    </h3>
                    
                    <div className="space-y-3">
                        <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                            <span className="text-sm font-medium text-gray-700">
                                Capacidad restante
                            </span>
                            <span className={`text-sm font-bold ${getProgressTextColor()}`}>
                                {userStats.restantes} mensajes
                            </span>
                        </div>
                        
                        <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                            <span className="text-sm font-medium text-gray-700">
                                Reinicio de lmite
                            </span>
                            <span className="text-sm text-gray-600">
                                Maana a las 00:00
                            </span>
                        </div>
                        
                        <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                            <span className="text-sm font-medium text-gray-700">
                                ltima actualizacin
                            </span>
                            <span className="text-sm text-gray-600">
                                {new Date().toLocaleTimeString()}
                            </span>
                        </div>
                    </div>
                </div>

                {/* Recomendaciones */}
                <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                    <h3 className="text-lg font-semibold text-gray-900 mb-4">
                        Recomendaciones
                    </h3>
                    
                    <div className="space-y-3">
                        {userStats.restantes > 100 && (
                            <div className="flex items-start space-x-3 p-3 bg-green-50 rounded-lg">
                                <CheckCircle className="h-5 w-5 text-green-600 mt-0.5" />
                                <div>
                                    <p className="text-sm font-medium text-green-800">
                                        Excelente capacidad
                                    </p>
                                    <p className="text-xs text-green-600">
                                        Puedes enviar mensajes con tranquilidad
                                    </p>
                                </div>
                            </div>
                        )}
                        
                        {userStats.restantes <= 100 && userStats.restantes > 20 && (
                            <div className="flex items-start space-x-3 p-3 bg-yellow-50 rounded-lg">
                                <Clock className="h-5 w-5 text-yellow-600 mt-0.5" />
                                <div>
                                    <p className="text-sm font-medium text-yellow-800">
                                        Capacidad moderada
                                    </p>
                                    <p className="text-xs text-yellow-600">
                                        Planifica tus envos para el resto del da
                                    </p>
                                </div>
                            </div>
                        )}
                        
                        {userStats.restantes <= 20 && (
                            <div className="flex items-start space-x-3 p-3 bg-red-50 rounded-lg">
                                <XCircle className="h-5 w-5 text-red-600 mt-0.5" />
                                <div>
                                    <p className="text-sm font-medium text-red-800">
                                        Capacidad limitada
                                    </p>
                                    <p className="text-xs text-red-600">
                                        Usa tus mensajes restantes con cuidado
                                    </p>
                                </div>
                            </div>
                        )}
                        
                        <div className="flex items-start space-x-3 p-3 bg-blue-50 rounded-lg">
                            <TrendingUp className="h-5 w-5 text-blue-600 mt-0.5" />
                            <div>
                                <p className="text-sm font-medium text-blue-800">
                                    Tip del da
                                </p>
                                <p className="text-xs text-blue-600">
                                    Revisa el historial para analizar patrones de envo
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default UserStats;
