import React, { createContext, useContext, useState, useEffect } from 'react';
import axios from 'axios';
import toast from 'react-hot-toast';

const AuthContext = createContext();

// Configurar axios
const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://172.17.249.85:3001';

axios.defaults.baseURL = API_BASE_URL;

// Interceptor para incluir token en todas las peticiones
axios.interceptors.request.use(
    (config) => {
        const token = localStorage.getItem('token');
        if (token) {
            // Verificar que el token tenga formato vlido antes de enviarlo
            const tokenParts = token.split('.');
            if (tokenParts.length === 3) {
                config.headers.Authorization = `Bearer ${token}`;
            } else {
                console.error('Token mal formado detectado, limpiando localStorage');
                localStorage.removeItem('token');
                localStorage.removeItem('usuario');
                window.location.href = '/login';
            }
        }
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// Interceptor para manejar errores de autenticacin
axios.interceptors.response.use(
    (response) => response,
    (error) => {
        if (error.response?.status === 401) {
            const errorMessage = error.response?.data?.error || '';
            
            // Si es un error de token mal formado, mostrar mensaje especfico
            if (errorMessage.includes('mal formado') || errorMessage.includes('malformed')) {
                toast.error('Sesin corrupta. Por favor, inicia sesin nuevamente.');
            } else if (errorMessage.includes('expirado') || errorMessage.includes('expired')) {
                toast.error('Sesin expirada. Por favor, inicia sesin nuevamente.');
            } else {
                toast.error('Sesin invlida. Por favor, inicia sesin nuevamente.');
            }
            
            localStorage.removeItem('token');
            localStorage.removeItem('usuario');
            
            // Solo redirigir si no estamos ya en la pgina de login
            if (window.location.pathname !== '/login') {
                window.location.href = '/login';
            }
        }
        return Promise.reject(error);
    }
);

export const AuthProvider = ({ children }) => {
    const [usuario, setUsuario] = useState(null);
    const [loading, setLoading] = useState(true);
    const [isAuthenticated, setIsAuthenticated] = useState(false);

    // Verificar token al cargar la aplicacin
    useEffect(() => {
        const initializeAuth = async () => {
            const token = localStorage.getItem('token');
            const storedUser = localStorage.getItem('usuario');

            if (token && storedUser) {
                try {
                    // Verificar que el token tenga el formato bsico de JWT
                    const tokenParts = token.split('.');
                    if (tokenParts.length !== 3) {
                        console.error('Token mal formado en localStorage:', token);
                        logout();
                        return;
                    }

                    // Verificar que el token sea vlido en el servidor
                    const response = await axios.get('/api/auth/verify');
                    if (response.data.success) {
                        setUsuario(JSON.parse(storedUser));
                        setIsAuthenticated(true);
                    } else {
                        logout();
                    }
                } catch (error) {
                    console.error('Error verificando token:', error.message);
                    logout();
                }
            }
            setLoading(false);
        };

        initializeAuth();
    }, []);

    const login = async (codigoUsuario, password) => {
        try {
            setLoading(true);
            
            const response = await axios.post('/api/auth/login', {
                codigoUsuario,
                password
            });

            if (response.data.success) {
                const { token, usuario } = response.data;
                
                // Guardar en localStorage
                localStorage.setItem('token', token);
                localStorage.setItem('usuario', JSON.stringify(usuario));
                
                // Actualizar estado
                setUsuario(usuario);
                setIsAuthenticated(true);
                
                toast.success(`Bienvenido, ${usuario.nombre}!`);
                return { success: true };
            } else {
                throw new Error(response.data.error || 'Error de autenticacin');
            }
        } catch (error) {
            const errorMessage = error.response?.data?.error || error.message || 'Error de conexin';
            toast.error(errorMessage);
            return { success: false, error: errorMessage };
        } finally {
            setLoading(false);
        }
    };

    const logout = () => {
        localStorage.removeItem('token');
        localStorage.removeItem('usuario');
        setUsuario(null);
        setIsAuthenticated(false);
        toast.success('Sesin cerrada correctamente');
    };

    // Funcin para limpiar datos corruptos
    const clearCorruptedData = () => {
        console.log('Limpiando datos corruptos de localStorage...');
        localStorage.removeItem('token');
        localStorage.removeItem('usuario');
        setUsuario(null);
        setIsAuthenticated(false);
    };

    const updateUsuario = (newUserData) => {
        const updatedUser = { ...usuario, ...newUserData };
        setUsuario(updatedUser);
        localStorage.setItem('usuario', JSON.stringify(updatedUser));
    };

    const value = {
        usuario,
        loading,
        isAuthenticated,
        login,
        logout,
        updateUsuario,
        clearCorruptedData
    };

    return (
        <AuthContext.Provider value={value}>
            {children}
        </AuthContext.Provider>
    );
};

export const useAuth = () => {
    const context = useContext(AuthContext);
    if (!context) {
        throw new Error('useAuth debe ser usado dentro de AuthProvider');
    }
    return context;
};

export default AuthContext;
