import { useState, useCallback } from 'react';
import axios from 'axios';

export const useDNIVerification = () => {
    const [loading, setLoading] = useState(false);
    const [userData, setUserData] = useState(null);
    const [error, setError] = useState(null);

    const verifyDNI = useCallback(async (dni) => {
        if (!dni || dni.length < 6) {
            setUserData(null);
            setError(null);
            return null;
        }

        setLoading(true);
        setError(null);

        try {
            const response = await axios.get(`/api/admin/verify-dni/${dni}`);
            
            if (response.data.found) {
                setUserData(response.data.data);
                return response.data.data;
            } else {
                setUserData(null);
                return null;
            }
        } catch (error) {
            console.error('Error verificando DNI:', error);
            setError('Error verificando DNI en sistema central');
            setUserData(null);
            return null;
        } finally {
            setLoading(false);
        }
    }, []);

    const clearData = useCallback(() => {
        setUserData(null);
        setError(null);
        setLoading(false);
    }, []);

    return {
        verifyDNI,
        clearData,
        loading,
        userData,
        error
    };
};

export default useDNIVerification;
