import React from 'react';
import { MessageCircle } from 'lucide-react';

const LoadingSpinner = ({ message = 'Cargando...' }) => {
    return (
        <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-600 via-purple-600 to-indigo-700">
            <div className="text-center">
                <div className="relative">
                    {/* Crculo exterior giratorio */}
                    <div className="w-20 h-20 border-4 border-white border-opacity-30 rounded-full animate-spin mx-auto"></div>
                    
                    {/* Crculo interior con cono */}
                    <div className="absolute inset-0 flex items-center justify-center">
                        <div className="w-12 h-12 bg-white rounded-full flex items-center justify-center shadow-lg">
                            <MessageCircle className="h-6 w-6 text-whatsapp-primary animate-pulse" />
                        </div>
                    </div>
                </div>
                
                <div className="mt-6">
                    <h3 className="text-white text-lg font-semibold">{message}</h3>
                    <p className="text-blue-100 text-sm mt-2">Por favor espera un momento...</p>
                </div>
            </div>
        </div>
    );
};

export default LoadingSpinner;
