import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { User, Lock, LogIn, MessageCircle } from 'lucide-react';
import FirstSetup from './FirstSetup';
import axios from 'axios';
import { toast } from 'react-hot-toast';

const Login = () => {
    const [formData, setFormData] = useState({
        codigoUsuario: '',
        password: ''
    });
    const [isLoading, setIsLoading] = useState(false);
    const [needsFirstSetup, setNeedsFirstSetup] = useState(false);
    const [checkingSetup, setCheckingSetup] = useState(true);
    const { login } = useAuth();

    // Limpiar localStorage en caso de tokens corruptos
    const clearStorageData = () => {
        localStorage.removeItem('token');
        localStorage.removeItem('usuario');
        toast.success('Datos de sesin limpiados. Intenta iniciar sesin nuevamente.');
        console.log('localStorage limpiado');
    };

    // Verificar si necesita configuracin inicial
    useEffect(() => {
        checkFirstSetup();
        
        // Tambin limpiar automticamente si hay un token mal formado
        const token = localStorage.getItem('token');
        if (token) {
            const tokenParts = token.split('.');
            if (tokenParts.length !== 3) {
                console.warn('Token mal formado detectado en el inicio, limpiando...');
                clearStorageData();
            }
        }
    }, []);

    const checkFirstSetup = async () => {
        try {
            const response = await axios.get('/api/admin/setup/check');
            setNeedsFirstSetup(response.data.needsFirstSetup);
        } catch (error) {
            console.error('Error verificando setup inicial:', error);
            toast.error('Error verificando configuracin del sistema');
        } finally {
            setCheckingSetup(false);
        }
    };

    const handleSetupComplete = (adminData) => {
        toast.success('Configuracin inicial completada!');
        setNeedsFirstSetup(false);
        // Opcional: Auto-login del admin recin creado
        // login(adminData.user.dni, 'password_provided_in_setup');
    };

    const handleChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value
        });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        if (!formData.codigoUsuario || !formData.password) {
            return;
        }

        setIsLoading(true);
        
        try {
            await login(formData.codigoUsuario, formData.password);
        } catch (error) {
            console.error('Error en login:', error);
        } finally {
            setIsLoading(false);
        }
    };

    // Mostrar pantalla de carga mientras verifica setup
    if (checkingSetup) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-600 via-purple-600 to-indigo-700">
                <div className="text-center">
                    <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-white mx-auto"></div>
                    <p className="mt-4 text-white">Verificando configuracin del sistema...</p>
                </div>
            </div>
        );
    }

    // Mostrar setup inicial si es necesario
    if (needsFirstSetup) {
        return <FirstSetup onSetupComplete={handleSetupComplete} />;
    }

    return (
        <div className="min-h-screen flex">
            {/* Seccin Izquierda - Imagen */}
            <div className="hidden lg:flex lg:w-1/2 relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-blue-600/20 via-purple-600/20 to-indigo-700/20 z-10"></div>
                <img 
                    src="/buhowsp.png" 
                    alt="WSP Bot Partner" 
                    className="w-full h-full object-cover"
                />
                {/* Overlay con informacin */}
                <div className="absolute inset-0 z-20 flex flex-col justify-end p-12 text-white">
                    <div className="mb-8">
                        <h1 className="text-4xl font-bold mb-4">
                            WSP BOT PARTNER
                        </h1>
                        <p className="text-xl opacity-90 mb-2">
                            Sistema de Envo de Mensajes
                        </p>
                        <p className="text-lg opacity-75">
                            Automatiza y gestiona tus comunicaciones de WhatsApp de manera eficiente
                        </p>
                    </div>
                    <div className="text-sm opacity-60">
                        DESARROLLO TI PARTNER
                    </div>
                </div>
            </div>

            {/* Seccin Derecha - Formulario */}
            <div className="w-full lg:w-1/2 flex items-center justify-center bg-gray-50 px-6 py-12">
                <div className="max-w-md w-full space-y-8">
                    {/* Header para pantallas mviles */}
                    <div className="text-center lg:hidden">
                        <div className="mx-auto h-16 w-16 bg-white rounded-full flex items-center justify-center shadow-lg mb-4">
                            <MessageCircle className="h-8 w-8 text-green-600" />
                        </div>
                        <h2 className="text-2xl font-bold text-gray-900 mb-2">
                            WSP BOT PARTNER
                        </h2>
                        <p className="text-sm text-gray-600">
                            Sistema de Envo de Mensajes
                        </p>
                    </div>

                    {/* Header para pantallas grandes */}
                    <div className="text-center hidden lg:block">
                        <div className="mx-auto h-16 w-16 bg-gradient-to-br from-blue-600 to-purple-600 rounded-full flex items-center justify-center shadow-lg mb-6">
                            <MessageCircle className="h-8 w-8 text-white" />
                        </div>
                        <h2 className="text-3xl font-bold text-gray-900 mb-2">
                            Iniciar Sesin
                        </h2>
                        <p className="text-gray-600">
                            Accede a tu cuenta para comenzar
                        </p>
                    </div>

                    {/* Formulario */}
                    <div className="bg-white rounded-xl shadow-lg p-8">
                        <form className="space-y-6" onSubmit={handleSubmit}>
                            <div>
                                <label htmlFor="codigoUsuario" className="block text-sm font-medium text-gray-700 mb-2">
                                    Cdigo de Usuario
                                </label>
                                <div className="relative">
                                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                                        <User className="h-5 w-5 text-gray-400" />
                                    </div>
                                    <input
                                        id="codigoUsuario"
                                        name="codigoUsuario"
                                        type="text"
                                        autoComplete="username"
                                        required
                                        value={formData.codigoUsuario}
                                        onChange={handleChange}
                                        className="block w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200 text-sm"
                                        placeholder="Ingresa tu cdigo de usuario"
                                        disabled={isLoading}
                                    />
                                </div>
                            </div>

                            <div>
                                <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-2">
                                    Contrasea
                                </label>
                                <div className="relative">
                                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                                        <Lock className="h-5 w-5 text-gray-400" />
                                    </div>
                                    <input
                                        id="password"
                                        name="password"
                                        type="password"
                                        autoComplete="current-password"
                                        required
                                        value={formData.password}
                                        onChange={handleChange}
                                        className="block w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-200 text-sm"
                                        placeholder="Ingresa tu contrasea"
                                        disabled={isLoading}
                                    />
                                </div>
                            </div>

                            <div>
                                <button
                                    type="submit"
                                    disabled={isLoading || !formData.codigoUsuario || !formData.password}
                                    className="group relative w-full flex justify-center py-3 px-4 border border-transparent text-sm font-medium rounded-lg text-white bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed transition duration-200 shadow-lg hover:shadow-xl"
                                >
                                    {isLoading ? (
                                        <div className="flex items-center">
                                            <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                                            Iniciando sesin...
                                        </div>
                                    ) : (
                                        <div className="flex items-center">
                                            <LogIn className="h-4 w-4 mr-2" />
                                            Iniciar Sesin
                                        </div>
                                    )}
                                </button>
                            </div>
                        </form>

                        {/* Informacin adicional */}
                        <div className="mt-6 text-center space-y-2">
                            <p className="text-xs text-gray-500">
                                Contacta al administrador si tienes problemas de acceso
                            </p>
                            
                            {/* Botn de emergencia para limpiar datos corruptos */}
                            <button
                                onClick={clearStorageData}
                                className="text-xs text-red-500 hover:text-red-700 underline"
                                type="button"
                                title="Limpiar datos de sesin corrupta"
                            >
                                Problemas de sesin? Limpiar datos
                            </button>
                        </div>
                    </div>

                    {/* Footer para mviles */}
                    <div className="text-center lg:hidden">
                        <p className="text-xs text-gray-500">
                            DESARROLLO TI PARTNER
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Login;