import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { toast } from 'react-hot-toast';
import useDNIVerification from '../hooks/useDNIVerification';

const UserManagement = () => {
    const [users, setUsers] = useState([]);
    const [loading, setLoading] = useState(true);
    const [showCreateForm, setShowCreateForm] = useState(false);
    const [editingUser, setEditingUser] = useState(null);
    const { verifyDNI, loading: dniLoading, userData: dniData, error: dniError, clearData } = useDNIVerification();
    const [pagination, setPagination] = useState({
        page: 0,
        limit: 10,
        total: 0,
        totalPages: 0
    });

    const [formData, setFormData] = useState({
        dni: '',
        nombre: '',
        apellido: '',
        password: '',
        rol: 'agente'
    });

    // Verificar DNI cuando cambie en modo creacin
    useEffect(() => {
        if (showCreateForm && !editingUser && formData.dni && formData.dni.length >= 6) {
            const timeoutId = setTimeout(async () => {
                const data = await verifyDNI(formData.dni);
                if (data) {
                    setFormData(prev => ({
                        ...prev,
                        nombre: data.nombre,
                        apellido: data.apellido
                    }));
                    toast.success('Datos encontrados en sistema central');
                }
            }, 500);

            return () => clearTimeout(timeoutId);
        }
    }, [formData.dni, showCreateForm, editingUser, verifyDNI]);

    useEffect(() => {
        loadUsers();
    }, [pagination.page]);

    const loadUsers = async () => {
        try {
            const token = localStorage.getItem('token');
            const response = await axios.get(`/api/admin/users?page=${pagination.page}&limit=${pagination.limit}`, {
                headers: { Authorization: `Bearer ${token}` }
            });

            setUsers(response.data.users);
            setPagination(response.data.pagination);
        } catch (error) {
            console.error('Error cargando usuarios:', error);
            toast.error('Error al cargar usuarios');
        } finally {
            setLoading(false);
        }
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));
    };

    const handleCreateUser = async (e) => {
        e.preventDefault();
        try {
            const token = localStorage.getItem('token');
            await axios.post('/api/admin/users', formData, {
                headers: { Authorization: `Bearer ${token}` }
            });

            toast.success('Usuario creado exitosamente');
            setShowCreateForm(false);
            setFormData({ dni: '', nombre: '', apellido: '', password: '', rol: 'agente' });
            loadUsers();
        } catch (error) {
            console.error('Error creando usuario:', error);
            toast.error(error.response?.data?.message || 'Error al crear usuario');
        }
    };

    const handleUpdateUser = async (e) => {
        e.preventDefault();
        try {
            const token = localStorage.getItem('token');
            const updateData = { ...formData };
            if (!updateData.password) {
                delete updateData.password;
            }

            await axios.put(`/api/admin/users/${editingUser.id}`, updateData, {
                headers: { Authorization: `Bearer ${token}` }
            });

            toast.success('Usuario actualizado exitosamente');
            setEditingUser(null);
            setFormData({ dni: '', nombre: '', apellido: '', password: '', rol: 'agente' });
            loadUsers();
        } catch (error) {
            console.error('Error actualizando usuario:', error);
            toast.error(error.response?.data?.message || 'Error al actualizar usuario');
        }
    };

    const handleDeleteUser = async (userId) => {
        if (!window.confirm('Ests seguro de que deseas eliminar este usuario?')) {
            return;
        }

        try {
            const token = localStorage.getItem('token');
            await axios.delete(`/api/admin/users/${userId}`, {
                headers: { Authorization: `Bearer ${token}` }
            });

            toast.success('Usuario eliminado exitosamente');
            loadUsers();
        } catch (error) {
            console.error('Error eliminando usuario:', error);
            toast.error(error.response?.data?.message || 'Error al eliminar usuario');
        }
    };

    const startEdit = (user) => {
        setEditingUser(user);
        setFormData({
            dni: user.dni,
            nombre: user.nombre,
            apellido: user.apellido,
            password: '',
            rol: user.rol
        });
    };

    const cancelEdit = () => {
        setEditingUser(null);
        setShowCreateForm(false);
        setFormData({ dni: '', nombre: '', apellido: '', password: '', rol: 'agente' });
        clearData(); // Limpiar datos de verificacin DNI
    };

    if (loading) {
        return (
            <div className="flex items-center justify-center p-8">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
            </div>
        );
    }

    return (
        <div className="space-y-6">
            <div className="flex justify-between items-center">
                <h2 className="text-2xl font-bold text-gray-900">Gestin de Usuarios</h2>
                <button
                    onClick={() => setShowCreateForm(true)}
                    className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors"
                >
                    Crear Usuario
                </button>
            </div>

            {/* Formulario de creacin/edicin */}
            {(showCreateForm || editingUser) && (
                <div className="bg-white p-6 rounded-lg shadow-md border">
                    <h3 className="text-lg font-semibold mb-4">
                        {editingUser ? 'Editar Usuario' : 'Crear Nuevo Usuario'}
                    </h3>
                    <form onSubmit={editingUser ? handleUpdateUser : handleCreateUser}>
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">
                                    DNI
                                </label>
                                <div className="relative">
                                    <input
                                        type="text"
                                        name="dni"
                                        value={formData.dni}
                                        onChange={handleInputChange}
                                        disabled={editingUser}
                                        className="w-full px-3 py-2 pr-10 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-100"
                                        required
                                    />
                                    {/* Indicadores de verificacin para modo creacin */}
                                    {!editingUser && (
                                        <div className="absolute inset-y-0 right-0 pr-3 flex items-center">
                                            {dniLoading && formData.dni.length >= 6 && (
                                                <svg className="animate-spin h-5 w-5 text-blue-500" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                                                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                                </svg>
                                            )}
                                            {dniData && !dniLoading && (
                                                <svg className="h-5 w-5 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                                                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                                                </svg>
                                            )}
                                            {!dniData && !dniLoading && formData.dni.length >= 6 && (
                                                <svg className="h-5 w-5 text-yellow-500" fill="currentColor" viewBox="0 0 20 20">
                                                    <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                                                </svg>
                                            )}
                                        </div>
                                    )}
                                </div>
                                {/* Mensajes de estado solo para modo creacin */}
                                {!editingUser && (
                                    <>
                                        {dniData && (
                                            <p className="mt-1 text-sm text-green-600">
                                                 Usuario encontrado en sistema central
                                            </p>
                                        )}
                                        {!dniData && !dniLoading && formData.dni.length >= 6 && (
                                            <p className="mt-1 text-sm text-yellow-600">
                                                 Usuario no encontrado en sistema central - se crear nuevo registro
                                            </p>
                                        )}
                                        {dniError && (
                                            <p className="mt-1 text-sm text-red-600">
                                                 {dniError}
                                            </p>
                                        )}
                                    </>
                                )}
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">
                                    Nombre
                                </label>
                                <input
                                    type="text"
                                    name="nombre"
                                    value={formData.nombre}
                                    onChange={handleInputChange}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                                    required
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">
                                    Apellido
                                </label>
                                <input
                                    type="text"
                                    name="apellido"
                                    value={formData.apellido}
                                    onChange={handleInputChange}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                                    required
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">
                                    Rol
                                </label>
                                <select
                                    name="rol"
                                    value={formData.rol}
                                    onChange={handleInputChange}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                                >
                                    <option value="agente">Agente</option>
                                    <option value="admin">Administrador</option>
                                </select>
                            </div>
                            <div className="md:col-span-2">
                                <label className="block text-sm font-medium text-gray-700 mb-1">
                                    Contrasea {editingUser && '(dejar en blanco para mantener actual)'}
                                </label>
                                <input
                                    type="password"
                                    name="password"
                                    value={formData.password}
                                    onChange={handleInputChange}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                                    required={!editingUser}
                                />
                            </div>
                        </div>
                        <div className="flex gap-3 mt-6">
                            <button
                                type="submit"
                                className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors"
                            >
                                {editingUser ? 'Actualizar' : 'Crear'}
                            </button>
                            <button
                                type="button"
                                onClick={cancelEdit}
                                className="bg-gray-500 text-white px-4 py-2 rounded-lg hover:bg-gray-600 transition-colors"
                            >
                                Cancelar
                            </button>
                        </div>
                    </form>
                </div>
            )}

            {/* Lista de usuarios */}
            <div className="bg-white rounded-lg shadow-md overflow-hidden">
                <div className="overflow-x-auto">
                    <table className="w-full">
                        <thead className="bg-gray-50">
                            <tr>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                    Usuario
                                </th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                    DNI
                                </th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                    Rol
                                </th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                    Mensajes Hoy
                                </th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                    Total Mensajes
                                </th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                    Acciones
                                </th>
                            </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                            {users.map((user) => (
                                <tr key={user.id}>
                                    <td className="px-6 py-4 whitespace-nowrap">
                                        <div className="text-sm font-medium text-gray-900">
                                            {user.nombre} {user.apellido}
                                        </div>
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                        {user.dni}
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap">
                                        <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                                            user.rol === 'admin' 
                                                ? 'bg-purple-100 text-purple-800' 
                                                : 'bg-green-100 text-green-800'
                                        }`}>
                                            {user.rol}
                                        </span>
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                        {user.mensajes_hoy || 0}
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                        {user.total_mensajes || 0}
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                                        <button
                                            onClick={() => startEdit(user)}
                                            className="text-blue-600 hover:text-blue-900 mr-3"
                                        >
                                            Editar
                                        </button>
                                        <button
                                            onClick={() => handleDeleteUser(user.id)}
                                            className="text-red-600 hover:text-red-900"
                                        >
                                            Eliminar
                                        </button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>

                {/* Paginacin */}
                {pagination.totalPages > 1 && (
                    <div className="bg-white px-4 py-3 border-t border-gray-200 sm:px-6">
                        <div className="flex justify-between items-center">
                            <div className="text-sm text-gray-700">
                                Mostrando {pagination.page * pagination.limit + 1} a{' '}
                                {Math.min((pagination.page + 1) * pagination.limit, pagination.total)} de{' '}
                                {pagination.total} usuarios
                            </div>
                            <div className="flex gap-2">
                                <button
                                    onClick={() => setPagination(prev => ({ ...prev, page: Math.max(0, prev.page - 1) }))}
                                    disabled={pagination.page === 0}
                                    className="px-3 py-1 border border-gray-300 rounded-md text-sm disabled:opacity-50 disabled:cursor-not-allowed"
                                >
                                    Anterior
                                </button>
                                <button
                                    onClick={() => setPagination(prev => ({ ...prev, page: Math.min(prev.totalPages - 1, prev.page + 1) }))}
                                    disabled={pagination.page >= pagination.totalPages - 1}
                                    className="px-3 py-1 border border-gray-300 rounded-md text-sm disabled:opacity-50 disabled:cursor-not-allowed"
                                >
                                    Siguiente
                                </button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

export default UserManagement;
