import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { 
    MessageCircle, 
    Users, 
    Send, 
    BarChart3, 
    Settings, 
    LogOut,
    User,
    Calendar,
    Clock,
    CheckCircle,
    XCircle,
    Smartphone
} from 'lucide-react';
import toast from 'react-hot-toast';

// Componentes del dashboard
import WhatsAppConnection from './WhatsAppConnection';
import BulkMessaging from './BulkMessaging';
import UserStats from './UserStats';
import MessageHistory from './MessageHistory';
import UserManagement from './UserManagement';
import AdminStats from './AdminStats';

const Dashboard = () => {
    const { usuario, logout } = useAuth();
    const [activeTab, setActiveTab] = useState('connection');
    const [isBulkSending, setIsBulkSending] = useState(false); // Estado de envo masivo
    const [userStats, setUserStats] = useState({
        enviadosHoy: 0,
        limiteDiario: 200,
        restantes: 200
    });

    const tabs = [
        { id: 'connection', name: 'Conexin WhatsApp', icon: Smartphone },
        { id: 'messaging', name: 'Envo Masivo', icon: Send },
        { id: 'history', name: 'Historial', icon: BarChart3 },
        { id: 'stats', name: 'Estadsticas', icon: Users },
        // Pestaas solo para administradores
        ...(usuario?.rol === 'admin' ? [
            { id: 'admin-stats', name: 'Estadsticas Generales', icon: BarChart3 },
            { id: 'user-management', name: 'Gestin Usuarios', icon: Settings }
        ] : [])
    ];

    // Funcin para manejar cambio de tab con validacin
    const handleTabChange = (tabId) => {
        if (isBulkSending && activeTab === 'messaging') {
            const confirmed = window.confirm(
                ' Hay un envo masivo en progreso.\n\n' +
                'El envo contina en segundo plano, pero se recomienda permanecer en esta vista para monitorear el progreso.\n\n' +
                'Ests seguro de que quieres cambiar de seccin?'
            );
            
            if (!confirmed) {
                return; // No cambiar de tab
            }
        }
        setActiveTab(tabId);
    };

    // Cargar estadsticas del usuario
    useEffect(() => {
        loadUserStats();
    }, []);

    const loadUserStats = async () => {
        try {
            const response = await fetch(`${process.env.REACT_APP_API_URL}/api/auth/limite-diario`, {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                }
            });
            
            if (response.ok) {
                const data = await response.json();
                console.log(' Dashboard - Respuesta completa del API:', data);
                if (data.success) {
                    console.log(' Dashboard - data.limite:', data.limite);
                    setUserStats(data.limite);
                    console.log(' Dashboard - Despus de setUserStats, userStats actual:', data.limite);
                }
            }
        } catch (error) {
            console.error('Error cargando estadsticas:', error);
        }
    };

    const handleLogout = () => {
        logout();
    };

    const refreshStats = () => {
        loadUserStats();
    };

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Header */}
            <header className="bg-white shadow-sm border-b border-gray-200">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="flex justify-between items-center h-16">
                        {/* Logo y ttulo */}
                        <div className="flex items-center">
                            <div className="flex-shrink-0">
                                <MessageCircle className="h-8 w-8 text-whatsapp-primary" />
                            </div>
                            <div className="ml-3">
                                <h1 className="text-xl font-semibold text-gray-900">WSP BOT PARTNER</h1>
                            </div>
                        </div>

                        {/* Info del usuario */}
                        <div className="flex items-center space-x-4">
                            {/* Indicador de rol */}
                            {usuario?.rol === 'admin' && (
                                <div className="hidden md:flex items-center bg-purple-100 text-purple-800 px-2 py-1 rounded-full text-xs font-medium">
                                    <Settings className="h-3 w-3 mr-1" />
                                    Administrador
                                </div>
                            )}

                            {/* Estadsticas rpidas */}
                            <div className="hidden md:flex items-center space-x-4 text-sm">
                                <div className="flex items-center text-gray-600">
                                    <Calendar className="h-4 w-4 mr-1" />
                                    <span>Hoy: {userStats.enviadosHoy}/{userStats.limiteDiario}</span>
                                </div>
                                <div className="flex items-center text-green-600">
                                    <CheckCircle className="h-4 w-4 mr-1" />
                                    <span>Restantes: {userStats.restantes}</span>
                                </div>
                            </div>

                            {/* Dropdown usuario */}
                            <div className="relative flex items-center space-x-3">
                                <div className="flex items-center space-x-3">
                                    <div className="text-right">
                                        <p className="text-sm font-medium text-gray-900">{usuario?.nombre}</p>
                                        <p className="text-xs text-gray-500">{usuario?.codigo_usuario}</p>
                                    </div>
                                    <div className="h-8 w-8 bg-gradient-to-r from-blue-500 to-purple-500 rounded-full flex items-center justify-center">
                                        <User className="h-4 w-4 text-white" />
                                    </div>
                                </div>
                                
                                <button
                                    onClick={handleLogout}
                                    className="p-2 text-gray-400 hover:text-gray-600 hover:bg-gray-100 rounded-lg transition-colors"
                                    title="Cerrar Sesin"
                                >
                                    <LogOut className="h-4 w-4" />
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </header>

            {/* Navegacin de pestaas */}
            <nav className="bg-white border-b border-gray-200">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="flex space-x-8 overflow-x-auto">
                        {tabs.map((tab) => {
                            const Icon = tab.icon;
                            return (
                                <button
                                    key={tab.id}
                                    onClick={() => handleTabChange(tab.id)}
                                    className={`flex items-center px-3 py-4 text-sm font-medium border-b-2 whitespace-nowrap transition-colors relative ${
                                        activeTab === tab.id
                                            ? 'border-blue-500 text-blue-600'
                                            : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                                    } ${isBulkSending && tab.id === 'messaging' ? 'bg-orange-50 border-orange-300 text-orange-700' : ''}`}
                                >
                                    <Icon className="h-4 w-4 mr-2" />
                                    {tab.name}
                                    {/* Indicador visual de envo en progreso */}
                                    {isBulkSending && tab.id === 'messaging' && (
                                        <div className="flex items-center ml-2">
                                            <div className="animate-spin rounded-full h-3 w-3 border-b border-orange-600"></div>
                                            <span className="ml-1 text-xs text-orange-600 font-medium">Enviando</span>
                                        </div>
                                    )}
                                    {/* Punto indicador para otros tabs cuando hay envo en progreso */}
                                    {isBulkSending && tab.id !== 'messaging' && (
                                        <span 
                                            className="absolute -top-1 -right-1 h-2 w-2 bg-orange-500 rounded-full animate-pulse" 
                                            title="Envo masivo en progreso"
                                        ></span>
                                    )}
                                </button>
                            );
                        })}
                    </div>
                </div>
            </nav>

            {/* Contenido principal */}
            <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
                {activeTab === 'connection' && (
                    <WhatsAppConnection onStatsUpdate={refreshStats} />
                )}
                {activeTab === 'messaging' && (
                    <BulkMessaging 
                        userStats={userStats} 
                        onStatsUpdate={refreshStats}
                        onSendingStateChange={setIsBulkSending}
                    />
                )}
                {activeTab === 'history' && (
                    <MessageHistory />
                )}
                {activeTab === 'stats' && (
                    <UserStats userStats={userStats} />
                )}
                {activeTab === 'admin-stats' && usuario?.rol === 'admin' && (
                    <AdminStats />
                )}
                {activeTab === 'user-management' && usuario?.rol === 'admin' && (
                    <UserManagement />
                )}
            </main>

            {/* Notificacin flotante de envo en progreso */}
            {isBulkSending && activeTab !== 'messaging' && (
                <div className="fixed bottom-4 right-4 bg-orange-500 text-white px-4 py-3 rounded-lg shadow-lg flex items-center space-x-3 z-50 animate-pulse">
                    <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                    <div className="flex flex-col">
                        <span className="text-sm font-medium">Envo masivo en progreso</span>
                        <span className="text-xs opacity-90">Click aqu para ver el progreso</span>
                    </div>
                    <button 
                        onClick={() => handleTabChange('messaging')}
                        className="ml-2 bg-white bg-opacity-20 hover:bg-opacity-30 rounded px-2 py-1 text-xs transition-colors"
                    >
                        Ver
                    </button>
                </div>
            )}
        </div>
    );
};

export default Dashboard;
