import React, { useState, useRef, useEffect } from 'react';
import { 
    Send, 
    Upload, 
    Users, 
    MessageSquare, 
    FileText, 
    AlertTriangle,
    CheckCircle,
    XCircle,
    Loader2,
    Download,
    Eye
} from 'lucide-react';
import toast from 'react-hot-toast';

const BulkMessaging = ({ userStats, onStatsUpdate, onSendingStateChange }) => {
    const [message, setMessage] = useState('');
    const [contacts, setContacts] = useState([]);
    const [isSending, setIsSending] = useState(false);
    const [sendResults, setSendResults] = useState(null);
    const [showPreview, setShowPreview] = useState(false);
    const [showAddModal, setShowAddModal] = useState(false);
    const [multipleNumbers, setMultipleNumbers] = useState('');
    const [newContactName, setNewContactName] = useState('');
    const [newContactNumber, setNewContactNumber] = useState('');
    const fileInputRef = useRef(null);
    const abortControllerRef = useRef(null); // Para cancelar requests

    // Cleanup al desmontar componente
    useEffect(() => {
        return () => {
            // Cancelar cualquier request en curso al cambiar de vista
            if (abortControllerRef.current) {
                abortControllerRef.current.abort();
                console.log(' Request de envo masivo cancelado por cambio de vista');
            }
        };
    }, []);

    // Proteccin contra cierre de pestaa durante envo
    useEffect(() => {
        console.log(` useEffect beforeunload ejecutado - isSending: ${isSending}`);
        
        const handleBeforeUnload = (e) => {
            console.log(` beforeunload triggered - isSending: ${isSending}`);
            if (isSending) {
                console.log(' Mostrando advertencia de cierre');
                // Mensaje estndar del navegador (no personalizable por seguridad)
                const message = 'Hay un envo masivo en progreso. Ests seguro de que quieres salir?';
                e.preventDefault();
                e.returnValue = message; // Para navegadores antiguos
                return message; // Para navegadores modernos
            }
        };

        // Agregar listener cuando est enviando
        if (isSending) {
            window.addEventListener('beforeunload', handleBeforeUnload);
            console.log(' Proteccin contra cierre de pestaa activada durante envo');
        }

        // Cleanup - remover listener
        return () => {
            window.removeEventListener('beforeunload', handleBeforeUnload);
            if (isSending) {
                console.log(' Proteccin contra cierre de pestaa desactivada');
            }
        };
    }, [isSending]); // Ejecutar cuando cambie isSending

    // Notificar al Dashboard sobre el estado de envo
    useEffect(() => {
        if (onSendingStateChange) {
            onSendingStateChange(isSending);
        }
    }, [isSending, onSendingStateChange]);

    // Validar si se puede enviar
    const canSend = contacts.length > 0 && message.trim() && 
                   contacts.length <= userStats.restantes && 
                   !isSending;

    const handleFileUpload = (event) => {
        const file = event.target.files[0];
        if (!file) return;

        const reader = new FileReader();
        reader.onload = (e) => {
            try {
                const text = e.target.result;
                const lines = text.split('\n').filter(line => line.trim());
                const newContacts = [];

                lines.forEach((line, index) => {
                    const trimmedLine = line.trim();
                    if (!trimmedLine) return;

                    // Detectar formato: "nombre,numero" o solo "numero"
                    if (trimmedLine.includes(',')) {
                        const [name, number] = trimmedLine.split(',').map(s => s.trim());
                        if (number) {
                            newContacts.push({
                                id: index,
                                name: name || 'Sin nombre',
                                number: number.replace(/\D/g, '') // Solo nmeros
                            });
                        }
                    } else {
                        // Solo nmero
                        const number = trimmedLine.replace(/\D/g, '');
                        if (number.length >= 9) { // Cambiado de 10 a 9 para aceptar nmeros peruanos
                            newContacts.push({
                                id: index,
                                name: 'Sin nombre',
                                number: number
                            });
                        }
                    }
                });

                setContacts(newContacts);
                toast.success(`${newContacts.length} contactos cargados correctamente`);
                
                if (newContacts.length > userStats.restantes) {
                    toast.warning(`Solo puedes enviar ${userStats.restantes} mensajes ms hoy`);
                }
            } catch (error) {
                console.error('Error procesando archivo:', error);
                toast.error('Error al procesar el archivo');
            }
        };

        reader.readAsText(file);
        event.target.value = ''; // Limpiar input
    };

    const addManualContact = () => {
        setShowAddModal(true);
    };

    const handleAddSingleContact = () => {
        if (newContactNumber) {
            const cleanNumber = newContactNumber.replace(/\D/g, '');
            if (cleanNumber.length >= 9) { // Cambiado para aceptar nmeros peruanos de 9 dgitos
                const newContact = {
                    id: Date.now(),
                    name: newContactName || 'Sin nombre',
                    number: cleanNumber
                };
                setContacts([...contacts, newContact]);
                toast.success('Contacto agregado');
                setNewContactName('');
                setNewContactNumber('');
                setShowAddModal(false);
            } else {
                toast.error('Nmero de telfono invlido (mnimo 9 dgitos)');
            }
        } else {
            toast.error('Nmero requerido');
        }
    };

    const handleAddMultipleContacts = () => {
        if (!multipleNumbers.trim()) {
            toast.error('Ingresa al menos un contacto');
            return;
        }

        const lines = multipleNumbers.trim().split('\n');
        const newContacts = [];

        lines.forEach((line, index) => {
            const trimmedLine = line.trim();
            if (trimmedLine) {
                if (trimmedLine.includes(',')) {
                    // Formato: numero,nombre
                    const [number, name] = trimmedLine.split(',').map(s => s.trim());
                    const cleanNumber = number.replace(/\D/g, '');
                    if (cleanNumber.length >= 9) { // Cambiado para aceptar nmeros peruanos
                        newContacts.push({
                            id: Date.now() + index,
                            name: name || 'Sin nombre',
                            number: cleanNumber
                        });
                    }
                } else {
                    // Solo nmero
                    const cleanNumber = trimmedLine.replace(/\D/g, '');
                    if (cleanNumber.length >= 9) { // Cambiado para aceptar nmeros peruanos
                        newContacts.push({
                            id: Date.now() + index,
                            name: 'Sin nombre',
                            number: cleanNumber
                        });
                    }
                }
            }
        });

        if (newContacts.length > 0) {
            setContacts([...contacts, ...newContacts]);
            toast.success(`${newContacts.length} contactos agregados`);
            setMultipleNumbers('');
            setShowAddModal(false);
        } else {
            toast.error('No se pudieron procesar los contactos. Verifica el formato.');
        }
    };

    const removeContact = (id) => {
        setContacts(contacts.filter(contact => contact.id !== id));
    };

    const clearContacts = () => {
        setContacts([]);
        setSendResults(null);
    };

    const downloadTemplate = () => {
        const template = `# Plantilla de contactos para WhatsApp Bot
# 
# NMEROS PERUANOS (9 dgitos - se agrega automticamente cdigo +51)
987654321,Juan Prez
912345678,Mara Garca
999888777

# NMEROS MEXICANOS (10 dgitos - se agrega automticamente cdigo +52)  
5551234567,Carlos Lpez
5559876543,Ana Rodrguez

# NMEROS CON CDIGO DE PAS COMPLETO
51987654321,Luis Fernndez
525551234567,Sofia Martnez

# SOLO NMEROS (sin nombres)
987654321
912345678
5551234567`;

        const blob = new Blob([template], { type: 'text/plain' });
        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = 'plantilla_contactos.txt';
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
        toast.success('Plantilla descargada');
    };

    const sendBulkMessages = async () => {
        if (!canSend) return;

        console.log(' Iniciando envo masivo - Activando protecciones...');
        setIsSending(true);
        setSendResults(null);

        // Crear nuevo AbortController para este envo
        abortControllerRef.current = new AbortController();

        try {
            const response = await fetch(`${process.env.REACT_APP_API_URL}/api/whatsapp/send-bulk`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    contacts: contacts.slice(0, userStats.restantes), // Limitar por el lmite diario
                    message: message.trim()
                }),
                signal: abortControllerRef.current.signal // Agregar signal para cancelacin
            });

            const data = await response.json();

            if (data.success) {
                if (data.backgroundProcess) {
                    // El envo se est procesando en segundo plano
                    toast.success(` Envo masivo iniciado en segundo plano: ${data.totalToSend} mensajes se estn enviando. Puedes cambiar de vista sin interrumpir el proceso.`);
                    setSendResults({
                        success: true,
                        backgroundProcess: true,
                        totalToSend: data.totalToSend,
                        message: data.message,
                        userId: data.userId,
                        userName: data.userName
                    });
                    
                    // MANTENER isSending = true para activar protecciones
                    // Implementar polling para verificar cuando termine el envo
                    let initialEnviados = userStats.enviados_hoy || 0;
                    console.log(` Polling iniciado - Enviados iniciales: ${initialEnviados}, Esperamos: ${initialEnviados + data.totalToSend}`);
                    
                    const checkSendingStatus = setInterval(async () => {
                        try {
                            // Verificar si las estadsticas se han actualizado
                            const statsResponse = await fetch(`${process.env.REACT_APP_API_URL}/api/auth/limite-diario`, {
                                headers: {
                                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                                }
                            });
                            
                            if (statsResponse.ok) {
                                const statsData = await statsResponse.json();
                                if (statsData.success) {
                                    const currentEnviados = statsData.limite.enviados_hoy || 0;
                                    console.log(` Polling - Inicial: ${initialEnviados}, Actual: ${currentEnviados}, Target: ${initialEnviados + data.totalToSend}`);
                                    
                                    // Si las estadsticas se han actualizado (incrementaron los enviados_hoy)
                                    // significa que el envo ha terminado
                                    if (currentEnviados >= initialEnviados + data.totalToSend) {
                                        clearInterval(checkSendingStatus);
                                        setIsSending(false);
                                        toast.success(' Envo masivo completado en segundo plano');
                                        console.log(' Protecciones desactivadas - envo completado (verificado por polling)');
                                        
                                        // Actualizar estadsticas locales
                                        if (onStatsUpdate) {
                                            onStatsUpdate();
                                        }
                                    }
                                }
                            }
                        } catch (pollError) {
                            console.log('Error en polling de estado:', pollError);
                        }
                    }, 10000); // Verificar cada 10 segundos
                    
                    // Timeout de seguridad mximo (en caso de que el polling falle)
                    setTimeout(() => {
                        clearInterval(checkSendingStatus);
                        setIsSending(false);
                        toast(' Protecciones desactivadas por timeout de seguridad', { icon: '' });
                        console.log(' Protecciones desactivadas por timeout de seguridad');
                    }, data.totalToSend * 15000 + 60000); // Tiempo estimado + 1 minuto extra
                    
                } else {
                    // Respuesta tradicional con resultados completos
                    setSendResults(data);
                    toast.success(` Envo completado: ${data.totalSent} exitosos, ${data.totalFailed} fallidos. WhatsApp permanece conectado.`);
                    // Para respuestas completas, s desactivar isSending inmediatamente
                    setIsSending(false);
                }
                
                // Actualizar estadsticas
                if (onStatsUpdate) {
                    onStatsUpdate();
                }
                
                // Limpiar formulario para evitar envos duplicados
                setMessage('');
                setContacts([]);
                
            } else {
                toast.error(data.error || 'Error al enviar mensajes');
                setIsSending(false);
            }
        } catch (error) {
            if (error.name === 'AbortError') {
                console.log(' Solicitud cancelada por cambio de vista (el envo contina en segundo plano)');
                toast('Vista cambiada - el envo contina en segundo plano si ya haba iniciado', { icon: '' });
            } else {
                console.error('Error enviando mensajes:', error);
                toast.error('Error de conexin al servidor');
                setIsSending(false); // Solo desactivar en caso de error real
            }
        } finally {
            // Solo limpiar el AbortController, no el estado isSending
            // porque podra estar en procesamiento en segundo plano
            abortControllerRef.current = null;
        }
    };

    // Funcin para desactivar manualmente las protecciones
    const cancelSendingProtection = () => {
        const confirmed = window.confirm(
            ' Ests seguro de que quieres desactivar las protecciones?\n\n' +
            'El envo puede continuar en el servidor, pero las advertencias se desactivarn.'
        );
        
        if (confirmed) {
            setIsSending(false);
            toast(' Protecciones desactivadas manualmente', { icon: '' });
            console.log(' Usuario desactiv protecciones manualmente');
        }
    };

    return (
        <div className="space-y-6">
            {/* Header con estadsticas */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                <div className="flex items-center justify-between mb-4">
                    <h2 className="text-xl font-semibold text-gray-900">Envo Masivo de Mensajes</h2>
                    <div className="flex items-center space-x-4 text-sm">
                        <div className="flex items-center text-blue-600">
                            <Users className="h-4 w-4 mr-1" />
                            <span>{contacts.length} contactos</span>
                        </div>
                        <div className="flex items-center text-green-600">
                            <CheckCircle className="h-4 w-4 mr-1" />
                            <span>{userStats.restantes} restantes hoy</span>
                        </div>
                    </div>
                </div>

                {/* Alerta de lmite */}
                {contacts.length > userStats.restantes && (
                    <div className="mb-4 p-4 bg-yellow-50 border border-yellow-200 rounded-lg">
                        <div className="flex items-center space-x-2">
                            <AlertTriangle className="h-5 w-5 text-yellow-600" />
                            <span className="text-sm font-medium text-yellow-800">
                                Solo se enviarn {userStats.restantes} mensajes de {contacts.length} contactos debido al lmite diario
                            </span>
                        </div>
                    </div>
                )}
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* Panel de mensaje */}
                <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                    <h3 className="text-lg font-semibold text-gray-900 mb-4">Mensaje</h3>
                    
                    <div className="space-y-4">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Contenido del mensaje
                            </label>
                            <textarea
                                value={message}
                                onChange={(e) => setMessage(e.target.value)}
                                rows="6"
                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none"
                                placeholder="Escribe aqu el mensaje que se enviar a todos los contactos..."
                            />
                            <div className="mt-2 flex justify-between items-start text-xs text-gray-500">
                                <div className="flex-1">
                                    <span>{message.length} caracteres</span>
                                    <div className="mt-1 text-green-600 bg-green-50 p-2 rounded text-xs">
                                        <strong> Personalizacin automtica:</strong> Cada mensaje ser nico con emoji aleatorio y variaciones para evitar deteccin como spam
                                    </div>
                                </div>
                                <button
                                    onClick={() => setShowPreview(!showPreview)}
                                    className="flex items-center space-x-1 text-blue-600 hover:text-blue-700 ml-2"
                                >
                                    <Eye className="h-3 w-3" />
                                    <span>Preview</span>
                                </button>
                            </div>
                        </div>

                        {/* Preview del mensaje */}
                        {showPreview && message && (
                            <div className="p-4 bg-gray-50 rounded-lg border">
                                <h4 className="text-sm font-medium text-gray-700 mb-3">Vista previa con personalizacin:</h4>
                                <div className="space-y-3">
                                    <div className="bg-white p-3 rounded-lg border shadow-sm">
                                        <p className="text-xs text-gray-500 mb-1">Mensaje original:</p>
                                        <div className="whitespace-pre-wrap text-sm text-gray-900">
                                            {message}
                                        </div>
                                    </div>
                                    <div className="bg-green-50 p-3 rounded-lg border border-green-200">
                                        <p className="text-xs text-green-600 mb-2">Ejemplos personalizados automticos:</p>
                                        <div className="space-y-2 text-sm">
                                            <div className="bg-white p-2 rounded border"> {message}</div>
                                            <div className="bg-white p-2 rounded border">{message} </div>
                                            <div className="bg-white p-2 rounded border"> Hola! {message}</div>
                                        </div>
                                        <p className="text-xs text-green-600 mt-2">* Cada contacto recibir una versin nica</p>
                                    </div>
                                </div>
                            </div>
                        )}
                    </div>
                </div>

                {/* Panel de contactos */}
                <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                    <div className="flex items-center justify-between mb-4">
                        <h3 className="text-lg font-semibold text-gray-900">Contactos</h3>
                        <div className="flex space-x-2">
                            <button
                                onClick={downloadTemplate}
                                className="inline-flex items-center px-3 py-1 text-xs font-medium text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors"
                            >
                                <Download className="h-3 w-3 mr-1" />
                                Plantilla
                            </button>
                            <button
                                onClick={addManualContact}
                                className="inline-flex items-center px-3 py-1 text-xs font-medium text-blue-700 bg-blue-100 rounded-lg hover:bg-blue-200 transition-colors"
                            >
                                <Users className="h-3 w-3 mr-1" />
                                Agregar
                            </button>
                        </div>
                    </div>

                    {/* Upload de archivo */}
                    <div className="mb-4">
                        <input
                            ref={fileInputRef}
                            type="file"
                            accept=".txt,.csv"
                            onChange={handleFileUpload}
                            className="hidden"
                        />
                        <button
                            onClick={() => fileInputRef.current?.click()}
                            className="w-full p-4 border-2 border-dashed border-gray-300 rounded-lg hover:border-blue-400 hover:bg-blue-50 transition-colors"
                        >
                            <div className="text-center">
                                <Upload className="h-8 w-8 text-gray-400 mx-auto mb-2" />
                                <p className="text-sm font-medium text-gray-700">
                                    Subir archivo de contactos
                                </p>
                                <p className="text-xs text-gray-500">
                                    Formatos: .txt, .csv<br/>
                                    <strong>Nmeros aceptados:</strong><br/>
                                     Per (9 dgitos): 987654321<br/>
                                     Con nombre: 987654321,Juan Prez
                                </p>
                            </div>
                        </button>
                    </div>

                    {/* Lista de contactos */}
                    {contacts.length > 0 && (
                        <div className="space-y-2">
                            <div className="flex items-center justify-between">
                                <span className="text-sm font-medium text-gray-700">
                                    Lista ({contacts.length} contactos)
                                </span>
                                <button
                                    onClick={clearContacts}
                                    className="text-xs text-red-600 hover:text-red-700"
                                >
                                    Limpiar todo
                                </button>
                            </div>
                            
                            <div className="max-h-48 overflow-y-auto border border-gray-200 rounded-lg">
                                {contacts.slice(0, 10).map((contact) => (
                                    <div key={contact.id} className="flex items-center justify-between p-2 border-b border-gray-100 last:border-b-0">
                                        <div className="flex-1 min-w-0">
                                            <p className="text-sm font-medium text-gray-900 truncate">
                                                {contact.name}
                                            </p>
                                            <p className="text-xs text-gray-500">
                                                {contact.number}
                                            </p>
                                        </div>
                                        <button
                                            onClick={() => removeContact(contact.id)}
                                            className="ml-2 text-red-400 hover:text-red-600"
                                        >
                                            <XCircle className="h-4 w-4" />
                                        </button>
                                    </div>
                                ))}
                                {contacts.length > 10 && (
                                    <div className="p-2 text-xs text-gray-500 text-center">
                                        y {contacts.length - 10} contactos ms...
                                    </div>
                                )}
                            </div>
                        </div>
                    )}
                </div>
            </div>

            {/* Botn de envo */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-4">
                        <MessageSquare className="h-8 w-8 text-blue-500" />
                        <div>
                            <h3 className="text-lg font-semibold text-gray-900">
                                Listo para enviar
                            </h3>
                            <p className="text-sm text-gray-600">
                                {contacts.length > 0 && message.trim() 
                                    ? `Se enviar a ${Math.min(contacts.length, userStats.restantes)} contactos`
                                    : 'Agrega contactos y escribe un mensaje'
                                }
                            </p>
                        </div>
                    </div>

                    <button
                        onClick={sendBulkMessages}
                        disabled={!canSend}
                        className={`inline-flex items-center px-6 py-3 border border-transparent text-base font-medium rounded-lg transition-colors ${
                            canSend
                                ? 'text-white bg-whatsapp-primary hover:bg-whatsapp-secondary'
                                : 'text-gray-400 bg-gray-100 cursor-not-allowed'
                        }`}
                    >
                        {isSending ? (
                            <>
                                <Loader2 className="h-5 w-5 mr-2 animate-spin" />
                                Enviando...
                            </>
                        ) : (
                            <>
                                <Send className="h-5 w-5 mr-2" />
                                Enviar Mensajes
                            </>
                        )}
                    </button>
                    
                    {/* Botn para desactivar protecciones */}
                    {isSending && (
                        <button
                            onClick={cancelSendingProtection}
                            className="ml-4 inline-flex items-center px-4 py-2 border border-orange-300 text-sm font-medium rounded-lg text-orange-700 bg-orange-50 hover:bg-orange-100 transition-colors"
                        >
                            <XCircle className="h-4 w-4 mr-2" />
                            Desactivar Protecciones
                        </button>
                    )}
                </div>
            </div>

            

            {/* Modal para agregar contactos */}
            {showAddModal && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-white rounded-xl shadow-xl max-w-md w-full mx-4 max-h-[90vh] overflow-y-auto">
                        <div className="p-6">
                            <div className="flex justify-between items-center mb-4">
                                <h3 className="text-lg font-semibold text-gray-900">Agregar Contactos</h3>
                                <button
                                    onClick={() => setShowAddModal(false)}
                                    className="text-gray-400 hover:text-gray-600"
                                >
                                    <XCircle className="h-6 w-6" />
                                </button>
                            </div>

                            <div className="space-y-4">
                                {/* Contacto individual */}
                                <div className="border-b border-gray-200 pb-4">
                                    <h4 className="text-sm font-medium text-gray-700 mb-3">Contacto Individual</h4>
                                    <div className="space-y-3">
                                        <div>
                                            <label className="block text-xs font-medium text-gray-700 mb-1">
                                                Nombre (opcional)
                                            </label>
                                            <input
                                                type="text"
                                                value={newContactName}
                                                onChange={(e) => setNewContactName(e.target.value)}
                                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-sm"
                                                placeholder="Nombre del contacto"
                                            />
                                        </div>
                                        <div>
                                            <label className="block text-xs font-medium text-gray-700 mb-1">
                                                Nmero de telfono
                                            </label>
                                            <input
                                                type="text"
                                                value={newContactNumber}
                                                onChange={(e) => setNewContactNumber(e.target.value)}
                                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-sm"
                                                placeholder="987654321 (Per) o 5551234567 (Mxico)"
                                            />
                                        </div>
                                        <button
                                            onClick={handleAddSingleContact}
                                            className="w-full px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors text-sm"
                                        >
                                            Agregar Contacto
                                        </button>
                                    </div>
                                </div>

                                {/* Mltiples contactos */}
                                <div>
                                    <h4 className="text-sm font-medium text-gray-700 mb-3">Mltiples Contactos</h4>
                                    <div className="space-y-3">
                                        <div>
                                            <label className="block text-xs font-medium text-gray-700 mb-1">
                                                Lista de contactos
                                            </label>
                                            <div className="text-xs text-gray-500 mb-2">
                                                <strong>Formatos aceptados:</strong><br/>
                                                 <strong>Per (9 dgitos):</strong> <code>987654321</code><br/>
                                                 <strong>Con nombre:</strong> <code>987654321,Juan Prez</code><br/>
                                                 Un contacto por lnea
                                            </div>
                                            <textarea
                                                value={multipleNumbers}
                                                onChange={(e) => setMultipleNumbers(e.target.value)}
                                                rows="6"
                                                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-sm font-mono"
                                                placeholder={"987654321,Juan Prez\n912345678,Mara Garca\n5551234567,Carlos Lpez"}
                                            />
                                        </div>
                                        <button
                                            onClick={handleAddMultipleContacts}
                                            className="w-full px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors text-sm"
                                        >
                                            Agregar Mltiples Contactos
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default BulkMessaging;
