import React, { useState, useEffect } from 'react';
import { 
    Smartphone, 
    Wifi, 
    WifiOff, 
    RefreshCw, 
    Power, 
    CheckCircle, 
    AlertCircle,
    QrCode,
    Loader2
} from 'lucide-react';
import QRCode from 'react-qr-code';
import toast from 'react-hot-toast';

const WhatsAppConnection = ({ onStatsUpdate }) => {
    const [connectionStatus, setConnectionStatus] = useState({
        isReady: false,
        hasQR: false,
        qrCode: null,
        isConnecting: false, // Nuevo estado para spinner
        status: 'disconnected' // disconnected, connecting, waiting_scan, processing, ready
    });
    const [isInitializing, setIsInitializing] = useState(false);
    const [isDisconnecting, setIsDisconnecting] = useState(false);
    const [qrScannedAt, setQrScannedAt] = useState(null); // Para trackear cuando se escane el QR

    // Verificar estado de conexin al cargar
    useEffect(() => {
        checkConnectionStatus();
        
        //  Polling optimizado - menos agresivo para evitar interferir con la conexin
        const getPollingInterval = () => {
            if (connectionStatus.status === 'processing' && qrScannedAt) {
                // Cuando QR fue escaneado, polling ms lento para no interferir
                const elapsed = Date.now() - qrScannedAt;
                if (elapsed < 20000) return 3000; // Primeros 20s: cada 3s
                if (elapsed < 60000) return 5000; // Hasta 1 min: cada 5s
                return 8000; // Despus: cada 8s
            }
            if (connectionStatus.hasQR && !connectionStatus.isReady) {
                return 3000; // 3 segundos cuando hay QR activo (menos agresivo)
            }
            if (connectionStatus.isConnecting && !qrScannedAt) {
                return 2000; // 2 segundos solo en inicializacin inicial
            }
            return connectionStatus.isReady ? 15000 : 8000; // Ms lento en general
        };

        const setupInterval = () => {
            return setInterval(checkConnectionStatus, getPollingInterval());
        };

        let interval = setupInterval();

        // Reconfigurar intervalo cuando cambie el estado
        const intervalRef = { current: interval };
        const updateInterval = () => {
            clearInterval(intervalRef.current);
            intervalRef.current = setupInterval();
        };

        // Actualizar intervalo cuando cambie el estado
        updateInterval();

        return () => clearInterval(intervalRef.current);
    }, [connectionStatus.hasQR, connectionStatus.isReady, connectionStatus.isConnecting]);

    const checkConnectionStatus = async () => {
        try {
            const response = await fetch(`${process.env.REACT_APP_API_URL}/api/whatsapp/status`, {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                }
            });

            if (response.ok) {
                const data = await response.json();
                if (data.success) {
                    // Crear objeto de estado actualizado
                    const newStatus = {
                        isReady: data.isReady,
                        hasQR: data.hasQR,
                        qrCode: data.qrCode,
                        isConnecting: data.isConnecting || false,
                        status: data.status || (data.isReady ? 'ready' : 
                                data.hasQR ? 'waiting_scan' : 
                                data.isConnecting ? 'connecting' : 'disconnected')
                    };
                    
                    //  Detectar cuando el QR desaparece (usuario escane) - SIMPLIFICADO
                    const qrWasScanned = connectionStatus.hasQR && !newStatus.hasQR && !newStatus.isReady && !qrScannedAt;
                    
                    if (qrWasScanned) {
                        console.log('QR escaneado - Usuario debe presionar Conectar nuevamente');
                        setQrScannedAt(Date.now()); // Solo para evitar mltiples detecciones
                        toast.success('QR escaneado! Presiona "Conectar" nuevamente para finalizar la conexin', { 
                            duration: 10000,
                            icon: ''
                        });
                    }
                    
                    //  Limpiar estado cuando se conecte exitosamente
                    if (newStatus.isReady && qrScannedAt) {
                        console.log(' Conexin exitosa - limpiando estado de escaneo');
                        setQrScannedAt(null);
                    }
                    //  Resetear estado de escaneo cuando finalmente se conecte
                    if (newStatus.isReady && qrScannedAt) {
                        console.log(' Conexin exitosa - limpiando estado de escaneo');
                        setQrScannedAt(null);
                    }
                    
                    // Solo actualizar si hay cambios significativos
                    const hasChanged = (
                        connectionStatus.isReady !== newStatus.isReady ||
                        connectionStatus.hasQR !== newStatus.hasQR ||
                        connectionStatus.isConnecting !== newStatus.isConnecting ||
                        connectionStatus.status !== newStatus.status ||
                        connectionStatus.qrCode !== newStatus.qrCode
                    );
                    
                    if (hasChanged) {
                        console.log('Estado WhatsApp actualizado:', newStatus);
                        setConnectionStatus(newStatus);
                        
                        // Notificaciones de estado
                        if (newStatus.isReady && !connectionStatus.isReady) {
                            toast.success('WhatsApp conectado exitosamente!');
                        } else if (newStatus.hasQR && !connectionStatus.hasQR && !qrWasScanned) {
                            toast.success('QR generado - Escanalo con tu telfono');
                        }
                    }
                }
            }
        } catch (error) {
            console.error('Error verificando estado:', error);
            // No mostrar toast en cada error para evitar spam
        }
    };

    const initializeWhatsApp = async () => {
        setIsInitializing(true);
        
        //  Si es un reintento despus de escanear QR, resetear el timestamp
        if (qrScannedAt) {
            console.log(' Reintentando conexin despus de escanear QR...');
            setQrScannedAt(null); // Resetear para evitar bucles
        }
        
        // Actualizar estado local inmediatamente para mostrar spinner
        setConnectionStatus(prev => ({
            ...prev,
            isConnecting: true,
            status: 'connecting',
            hasQR: false,
            qrCode: null
        }));
        
        try {
            const response = await fetch(`${process.env.REACT_APP_API_URL}/api/whatsapp/initialize`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Content-Type': 'application/json'
                }
            });

            const data = await response.json();
            
            if (data.success) {
                toast.success(data.message || 'Conectando a WhatsApp...');
                // Verificar estado inmediatamente
                setTimeout(checkConnectionStatus, 1000);
            } else {
                toast.error(data.error || 'Error al inicializar WhatsApp');
                // Restaurar estado en caso de error
                setConnectionStatus(prev => ({
                    ...prev,
                    isConnecting: false,
                    status: 'disconnected'
                }));
            }
        } catch (error) {
            console.error('Error inicializando WhatsApp:', error);
            toast.error('Error de conexin al servidor');
            setConnectionStatus(prev => ({
                ...prev,
                isConnecting: false,
                status: 'disconnected'
            }));
        } finally {
            setIsInitializing(false);
        }
    };

    const disconnectWhatsApp = async () => {
        setIsDisconnecting(true);
        try {
            const response = await fetch(`${process.env.REACT_APP_API_URL}/api/whatsapp/disconnect`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Content-Type': 'application/json'
                }
            });

            const data = await response.json();
            
            if (data.success) {
                toast.success('WhatsApp desconectado correctamente');
                setConnectionStatus({
                    isReady: false,
                    hasQR: false,
                    qrCode: null
                });
                // Actualizar estadsticas despus de desconectar
                if (onStatsUpdate) {
                    onStatsUpdate();
                }
            } else {
                toast.error(data.error || 'Error al desconectar WhatsApp');
            }
        } catch (error) {
            console.error('Error desconectando WhatsApp:', error);
            toast.error('Error de conexin al servidor');
        } finally {
            setIsDisconnecting(false);
        }
    };

    const forceNewSession = async () => {
        const confirmed = window.confirm(
            'Est seguro de que desea limpiar la sesin?\n\n' +
            'Esto eliminar las credenciales guardadas y deber escanear el QR nuevamente.'
        );
        
        if (!confirmed) return;

        setIsDisconnecting(true);
        try {
            const response = await fetch(`${process.env.REACT_APP_API_URL}/api/whatsapp/force-new-session`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Content-Type': 'application/json'
                }
            });

            const data = await response.json();
            
            if (data.success) {
                toast.success('Sesin limpiada exitosamente. Puede conectar WhatsApp nuevamente.');
                setConnectionStatus({
                    isReady: false,
                    hasQR: false,
                    qrCode: null,
                    isConnecting: false,
                    status: 'disconnected'
                });
            } else {
                toast.error(data.error || 'Error al limpiar sesin');
            }
        } catch (error) {
            console.error('Error limpiando sesin:', error);
            toast.error('Error de conexin al servidor');
        } finally {
            setIsDisconnecting(false);
        }
    };

    const getStatusColor = () => {
        if (connectionStatus.isReady) return 'text-green-500';
        if (connectionStatus.status === 'processing') return 'text-green-500';
        if (connectionStatus.hasQR) return 'text-yellow-500';
        if (connectionStatus.isConnecting) return 'text-blue-500';
        return 'text-gray-400';
    };

    const getStatusText = () => {
        if (connectionStatus.isReady) return 'Conectado';
        if (connectionStatus.status === 'processing') return 'Procesando conexin...';
        if (connectionStatus.hasQR) return 'Esperando escaneo QR';
        if (connectionStatus.isConnecting) return 'Conectando...';
        return 'Desconectado';
    };

    return (
        <div className="space-y-6">
            {/* Header con estado */}
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-4">
                        <div className={`p-3 rounded-full ${
                            connectionStatus.isReady ? 'bg-green-100' : 
                            connectionStatus.hasQR ? 'bg-yellow-100' : 
                            connectionStatus.isConnecting ? 'bg-blue-100' : 'bg-gray-100'
                        }`}>
                            {connectionStatus.isConnecting ? (
                                <Loader2 className="h-6 w-6 text-blue-500 animate-spin" />
                            ) : (
                                <Smartphone className={`h-6 w-6 ${getStatusColor()}`} />
                            )}
                        </div>
                        <div>
                            <h2 className="text-xl font-semibold text-gray-900">Estado de WhatsApp</h2>
                            <div className="flex items-center space-x-2 mt-1">
                                {connectionStatus.isReady ? (
                                    <Wifi className="h-4 w-4 text-green-500" />
                                ) : (
                                    <WifiOff className="h-4 w-4 text-gray-400" />
                                )}
                                <span className={`text-sm font-medium ${getStatusColor()}`}>
                                    {getStatusText()}
                                </span>
                                {connectionStatus.userId && (
                                    <span className="text-xs text-gray-500 ml-2">
                                        (Usuario: {connectionStatus.userId})
                                    </span>
                                )}
                            </div>
                        </div>
                    </div>

                    <div className="flex space-x-3">
                        <button
                            onClick={checkConnectionStatus}
                            className="inline-flex items-center px-3 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 transition-colors"
                        >
                            <RefreshCw className="h-4 w-4 mr-2" />
                            Actualizar
                        </button>

                        {/* Botn especial para continuar conexin despus de escanear QR */}
                        {qrScannedAt && !connectionStatus.isReady && (
                            <button
                                onClick={initializeWhatsApp}
                                disabled={connectionStatus.isConnecting}
                                className="inline-flex items-center px-4 py-2 border border-transparent rounded-lg text-sm font-medium text-white bg-green-600 hover:bg-green-700 disabled:opacity-50 transition-colors animate-pulse"
                            >
                                {connectionStatus.isConnecting ? (
                                    <>
                                        <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                                        Conectando...
                                    </>
                                ) : (
                                    <>
                                        <CheckCircle className="h-4 w-4 mr-2" />
                                        Continuar Conexin
                                    </>
                                )}
                            </button>
                        )}

                        {/* Botn para forzar nueva sesin cuando hay problemas */}
                        {!connectionStatus.isReady && !connectionStatus.isConnecting && !qrScannedAt && (
                            <button
                                onClick={forceNewSession}
                                disabled={isDisconnecting}
                                className="inline-flex items-center px-3 py-2 border border-orange-300 rounded-lg text-sm font-medium text-orange-700 bg-orange-50 hover:bg-orange-100 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                            >
                                {isDisconnecting ? (
                                    <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                                ) : (
                                    <RefreshCw className="h-4 w-4 mr-2" />
                                )}
                                Nueva Sesin
                            </button>
                        )}

                        {connectionStatus.isReady ? (
                            <button
                                onClick={disconnectWhatsApp}
                                disabled={isDisconnecting}
                                className="inline-flex items-center px-4 py-2 border border-transparent rounded-lg text-sm font-medium text-white bg-red-600 hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                            >
                                {isDisconnecting ? (
                                    <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                                ) : (
                                    <Power className="h-4 w-4 mr-2" />
                                )}
                                Desconectar
                            </button>
                        ) : (
                            <button
                                onClick={initializeWhatsApp}
                                disabled={isInitializing || connectionStatus.isConnecting}
                                className="inline-flex items-center px-4 py-2 border border-transparent rounded-lg text-sm font-medium text-white bg-whatsapp-primary hover:bg-whatsapp-secondary disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                            >
                                {(isInitializing || connectionStatus.isConnecting) ? (
                                    <>
                                        <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                                        {connectionStatus.isConnecting ? 'Conectando...' : 'Iniciando...'}
                                    </>
                                ) : (
                                    <>
                                        <Power className="h-4 w-4 mr-2" />
                                        Conectar
                                    </>
                                )}
                            </button>
                        )}
                    </div>
                </div>
            </div>

            {/* Estados de conexin */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Estado de Procesando Conexin (despus de escanear QR) */}
                {connectionStatus.status === 'processing' && qrScannedAt && (
                    <div className="md:col-span-2">
                        <div className="bg-gradient-to-r from-green-50 to-blue-50 rounded-xl shadow-lg p-8 border-2 border-green-200 relative overflow-hidden">
                            {/* Overlay animado */}
                            <div className="absolute inset-0 bg-gradient-to-r from-green-400/20 to-blue-400/20 animate-pulse"></div>
                            
                            <div className="relative text-center">
                                <div className="flex items-center justify-center mb-6">
                                    <div className="relative">
                                        <Loader2 className="h-16 w-16 text-green-500 animate-spin" />
                                        <CheckCircle className="h-8 w-8 text-green-600 absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2" />
                                    </div>
                                </div>
                                <h3 className="text-2xl font-bold text-gray-900 mb-3">
                                     QR Escaneado Exitosamente!
                                </h3>
                                <p className="text-lg text-gray-700 mb-4">
                                    Finalizando conexin con WhatsApp...
                                </p>
                                <div className="max-w-md mx-auto">
                                    <div className="animate-pulse space-y-2">
                                        <div className="h-3 bg-green-300 rounded w-full"></div>
                                        <div className="h-3 bg-blue-300 rounded w-3/4 mx-auto"></div>
                                        <div className="h-3 bg-green-300 rounded w-1/2 mx-auto"></div>
                                    </div>
                                </div>
                                <p className="text-sm text-gray-600 mt-4">
                                    Si el proceso toma mucho tiempo, presiona "Continuar Conexin" arriba
                                </p>
                            </div>
                        </div>
                    </div>
                )}

                {/* Estado de Conectando */}
                {connectionStatus.isConnecting && !connectionStatus.hasQR && !qrScannedAt && (
                    <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                        <div className="text-center">
                            <div className="flex items-center justify-center mb-4">
                                <Loader2 className="h-12 w-12 text-blue-500 animate-spin" />
                            </div>
                            <h3 className="text-lg font-semibold text-gray-900 mb-2">
                                Conectando a WhatsApp
                            </h3>
                            <p className="text-sm text-gray-600 mb-4">
                                Estableciendo conexin y generando cdigo QR...
                            </p>
                            <div className="animate-pulse">
                                <div className="h-2 bg-blue-200 rounded w-3/4 mx-auto mb-2"></div>
                                <div className="h-2 bg-blue-200 rounded w-1/2 mx-auto"></div>
                            </div>
                        </div>
                    </div>
                )}

                {/* QR Code */}
                {connectionStatus.hasQR && connectionStatus.qrCode && (
                    <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                        <div className="text-center">
                            <QrCode className="h-8 w-8 text-blue-500 mx-auto mb-4" />
                            <h3 className="text-lg font-semibold text-gray-900 mb-2">
                                Escanea el cdigo QR
                            </h3>
                            <p className="text-sm text-gray-600 mb-6">
                                Abre WhatsApp en tu telfono y escanea este cdigo para conectar. Recuerda pulsar conectar despues del escaneo para finalizar la sincronizacin.
                            </p>
                            
                            <div className="qr-container inline-block bg-white p-4 rounded-lg shadow-inner">
                                <QRCode
                                    value={connectionStatus.qrCode}
                                    size={200}
                                    style={{ height: "auto", maxWidth: "100%", width: "100%" }}
                                    level="M"
                                    includeMargin={true}
                                />
                            </div>
                            
                            <div className="mt-4 p-3 bg-yellow-50 rounded-lg">
                                <div className="flex items-center justify-center space-x-2">
                                    <AlertCircle className="h-4 w-4 text-yellow-600" />
                                    <span className="text-xs text-yellow-800">
                                        El cdigo QR se actualiza automticamente
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                )}

                {/* Estado de conexin */}
                <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-200">
                    <h3 className="text-lg font-semibold text-gray-900 mb-4">
                        Informacin de Conexin
                    </h3>
                    
                    <div className="space-y-4">
                        <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                            <span className="text-sm font-medium text-gray-700">Estado</span>
                            <div className="flex items-center space-x-2">
                                {connectionStatus.isReady ? (
                                    <CheckCircle className="h-4 w-4 text-green-500" />
                                ) : (
                                    <AlertCircle className="h-4 w-4 text-gray-400" />
                                )}
                                <span className={`text-sm font-medium ${getStatusColor()}`}>
                                    {getStatusText()}
                                </span>
                            </div>
                        </div>

                        <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                            <span className="text-sm font-medium text-gray-700">QR Disponible</span>
                            <span className={`text-sm font-medium ${connectionStatus.hasQR ? 'text-yellow-600' : 'text-gray-500'}`}>
                                {connectionStatus.hasQR ? 'S' : 'No'}
                            </span>
                        </div>

                        {connectionStatus.isReady && (
                            <div className="p-4 bg-green-50 rounded-lg">
                                <div className="flex items-center space-x-2">
                                    <CheckCircle className="h-5 w-5 text-green-600" />
                                    <span className="text-sm font-medium text-green-800">
                                        WhatsApp conectado y listo para enviar mensajes
                                    </span>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            </div>

            {/* Instrucciones */}
            {!connectionStatus.isReady && (
                <div className="space-y-4">
                    <div className="bg-blue-50 rounded-xl p-6 border border-blue-200">
                        <h3 className="text-lg font-semibold text-blue-900 mb-4">
                            Cmo conectar WhatsApp
                        </h3>
                        <ol className="list-decimal list-inside space-y-2 text-sm text-blue-800">
                            <li>Haz clic en el botn "Conectar" para inicializar WhatsApp</li>
                            <li>Aparecer un cdigo QR en pantalla</li>
                            <li>Abre WhatsApp en tu telfono</li>
                            <li>Ve a Configuracin  Dispositivos vinculados</li>
                            <li>Toca "Vincular un dispositivo" y escanea el cdigo QR</li>
                            <li>Pulsar nuevamente Conectar y listo!</li>
                        </ol>
                    </div>
                
                </div>
            )}
        </div>
    );
};

export default WhatsAppConnection;
